<template>
  <div class="detail" ref="wrapper">
    <div class="bg">
      <div class="dHead">
        <span class="back" @click="back"><i class="mintui mintui-back"></i></span>
      </div>
      <div class="fd" id="fd" :class="{top:istop}">
        {{obj.title}}
      </div>
      <div class="bookDe">
        <div class="books">
          <div class="bLeft">
             <img :src="staUrl + obj.cover" class="bImg">
          </div>
          <div class="bRight">
            <div>{{obj.title}}</div>
            <div class="sugge">{{obj.author}}</div>
            <div class="sugge">{{obj.minorCate}}</div>
            <div class="sugge">
              <div class="star-rating">
               <div class="star-rating-top" id="star">
                  <span>★</span>
                  <span>★</span>
                  <span>★</span>
                  <span>★</span>
                  <span>★</span>
               </div>
               <div class="star-rating-bottom">
                  <span>★</span>
                  <span>★</span>
                  <span>★</span>
                  <span>★</span>
                  <span>★</span>
               </div>
              </div>
            </div>
            <div class="sugge"><span>{{obj.wordCount | convert}}</span>  | 连载</div>
          </div>
        </div>
      </div>
    </div>
    <div class="nav">
      <div class="item4">
        <p>0</p>
        <p>月票</p>
      </div>
      <div class="item4">
        <p>0</p>
        <p>推荐票</p>
      </div>
      <div class="item4">
        <p>0</p>
        <p>打赏</p>
      </div>
      <div class="item4">
        <p>0</p>
        <p>粉丝</p>
      </div>
    </div>
    <div class="synopsis">
      <p>简介</p>
      <p class="sp" id="slide" @click="slide">{{obj.longIntro}}</p>
      <i @click="slide" v-show="showDown" class="mintui mintui-xiangxia"></i>
    </div>
    <div class="content">
      <mt-cell title="目录" is-link class="mt-cell" @click.native="getChapter(obj._id)">
        <span>连载至{{obj.chaptersCount}}章</span>&nbsp;
        <span>更新于{{obj.updated | fmtDate('yyyy-MM-dd')}}</span>
      </mt-cell>
    </div>
    <div class="content">
      <mt-cell title="作品荣誉"  value="谢主隆恩" is-link class="mt-cell">
      </mt-cell>
    </div>
    <div class="content" style="background-color:#fff;">
      <mt-cell title="书评"  :value="totalCount+'人评论'" is-link class="mt-cell">
      </mt-cell>
      <div class="comments">
        <div class="comment" v-for="(r,index) in reviews" v-if="index < 3" @click="commentDetails(r)">
          <div class="bCom">
            <img class="img" :src="staUrl+r.author.activityAvatar">
          </div>
          <div class="rCom">
            <p style="color:#2399bf">{{r.author.nickname}}</p>
            <p class="sp">{{r.content}}</p>
            <p class="wsp">
              <span>{{r.commentCount}}回复</span>
              <span>{{r.created | fmtDate('yyyy-MM-dd')}}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <mt-cell title="同类推荐" value="更多" is-link class="mt-cell">
      </mt-cell>
      <div style="overflow:hidden">
        <div class="boxs sx" style="margin-bottom: -6px;overflow-x: scroll;">
          <div  class="box pdr" v-for="(b,index) in bookList" v-if="index -1 <6">
            <div >
               <img :src="staUrl+b.cover">
            </div>
            <div>
              {{b.title}}
            </div>
            <div>
            16万人读过
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <mt-cell title="书友还看过" value="更多" is-link class="mt-cell">
      </mt-cell>
      <div style="overflow:hidden">
        <div class="boxs sx" style="margin-bottom: -6px;overflow-x: scroll;">
          <div  class="box pdr" v-for="(b,index) in bookList" v-if="index -1 >6">
            <div >
               <img :src="staUrl+b.cover">
            </div>
            <div>
              {{b.title}}
            </div>
            <div>
            16万人读过
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="copyright">
      <p>版权信息</p>
      <p>2017-09-21上架</p>
      <p>本作品由起点中文网进行电子制作与发行</p>
      <p>版权所有-侵权必究</p>
    </div>
    <div class="flex fixed items">
      <div class="item bs">
        <i class="mintui mintui-xiazai"></i>
        <p>下载</p>
      </div>
      <div class="item bs">
        <p>立即阅读</p>
      </div>
      <div class="item bs">
        <i class="mintui mintui-shujia"></i>
        <p>加入书架</p>
      </div>
    </div>
  </div>
</template>
<script>
  import {Indicator} from 'mint-ui';
  import * as config from '../config/config.js';
  export default {
    name:'detail',
    data(){
      return {
        istop:false,
        bookList:[],
        obj:{},
        showDown:true,
        staUrl:'http://statics.zhuishushenqi.com',
        id:'',
        reviews:[],
        totalCount:''
      }
    },
    mounted (){
      window.addEventListener('scroll', this.handleScroll);
    },
    created (){
      let url = config.API_BASE_URL+'/book/'+this.$route.params.id;
      this.axios.get(url)
      .then((res)=>{
        this.obj = res.data;
        this.updated = this.obj.lastChapter + '   ' + this.obj.updated;
        this.getStar(res.data.rating.score);
        this.$store.state.bId = this.$route.params.id;
        this.id = this.$route.params.id;
      });

      let url1 = config.API_BASE_URL+'/book/'+this.$route.params.id+'/recommend';
      this.axios.get(url1)
      .then((res)=>{
         this.bookList = res.data.books;
      });

      let url2 = config.API_BASE_URL + '/post/review/best-by-book?book=' + this.$route.params.id;
      this.axios.get(url2)
      .then((res)=>{
         this.reviews = res.data.reviews;
         this.totalCount = res.data.total;
      });

      // Indicator.open('加载中');
      // this.$axios.post('/vueBook/bookList',{})
      // .then((res)=>{
      //   if(res.data.state === 200){
      //     Indicator.close();
      //     this.bookList = res.data.data;
      //     this.bookList.map((value,key,obj)=>{
      //       if(value.name === this.$route.params.name){
      //         this.obj = value;
      //       }
      //     });
      //   }
      // })
      // .catch((err)=>{
      //   console.log(err);
      // });
      // console.log(this.$route.params.name);
    },
    methods:{
      handleScroll(){
         var fd = document.getElementById('fd');
         var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
         if(scrollTop >=200){
            this.istop = true;
          }else {
            this.istop = false;
          }
      },
      back(){
        this.$router.go(-1);
      },
      getStar(rating){
        var star = document.getElementById('star');
        star.style.width = (rating/10)*100 + '%';
      },
      slide(){
        var p = document.getElementById('slide');
        if(this.showDown){
          p.style.display = 'block';
        }else {
          p.style.display = '-webkit-box';
        }
        this.showDown = !this.showDown;
      },
      getChapter(){
        this.$router.push({
          name:'chapter',
          params:{id:this.id}
        });
      },
      commentDetails(r){
        this.$router.push({
          name:'comment',
          params:{cid:r._id}
        });
      }
    }
  }
</script>
<style lang="less" scoped>
.wsp {
  font-size: 12px;
  color:#666;
  padding-bottom: 0.5rem;
  span:first-child{
    padding-right: 0.5rem;
  }
}
.comment {
  clear:both;
  overflow: hidden;
}
.bCom{
  float: left;
  padding-left: 1rem;
  width: 18%;
}
.rCom {
  float: right;
  font-size: 14px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 1rem;
  width: 75%;
}
.rCom p{
  width: 226px;
  word-break: break-all;
}
.img {
  width: 3rem;
  height: 3rem;
  border-radius: 1.5rem;
}
.detail {
  background-color:#ddd;
  font-family: SimSun;
}
.nav {
  display: flex;
  justify-content: space-around;
  padding: 0.5rem 0;
  background: #fff;
  margin-bottom: 0.5rem;
}
.item4{
  width: 25%;
}
p,h4 {
  margin:0;
  padding:0;
}

.synopsis {
  background-color:#fff;
  padding: 1rem;
  text-align: left;
  margin-bottom: 0.5rem;
}
.synopsis i{
  position: relative;
  float: right;
  right: -13px;
  top:-21px;
}
.copyright {
  background-color: #fff;
  text-align: left;
  margin-bottom: 3rem;
}
.copyright p:first-child {
  font-size: 14px;
  color:#000;
}
.copyright p {
  padding: 3px 3px 3px 16px;
  font-size: 12px;
  color:#888;
}
.sp {
  margin: 0.5rem 0;
  display: -webkit-box;
  text-overflow: hidden;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  color:#444;
  font-size:12px;
}
  .bg {
    background: url('../../static/bc.png') no-repeat bottom;
    background-size: 100% 100%;
    background-attachment: fixed;
    font-family: SimSun;
  }
  .dHead {
    height: 2.5rem;
    line-height: 2.5rem;
    position: fixed;
    width: 100%;
    top: 0;
    left:0;
    background: url('../../static/bc.png') no-repeat;
    color: #fff;
    z-index:3;
  }
  .dHead span {
    float: left;
  }
  .back {
    padding: 0 1rem;
  }
  .bookDe {
    padding-top: 2.5rem;
  }
  .fd {
    text-align: left;
    margin-left: 3rem;
    display: none;
    position: relative;
    height: 2.5rem;
    line-height: 2.5rem;
    width: 100%;
    color:#fff;
  }
  .top {
    display: block;
    top:0;
    z-index:4;
    position: fixed;
  }
.star-rating {
        unicode-bidi: bidi-override;
        color: #ddd;
        font-size: 0;
        height: 20px;
        position: relative;
        display: table;
        padding: 0;
        text-shadow: 0px 1px 0 #a2a2a2;
    }

    .star-rating span {
        font-size: 18px;
    }

    .star-rating-top {
        color: #FFD700;
        padding: 0;
        position: absolute;
        z-index: 1;
        display: block;
        top: 0;
        left: 0;
        overflow: hidden;
        white-space: nowrap;
    }

    .star-rating-bottom {
        padding: 0;
        display: block;
        z-index: 0;
    }

</style>


